'use client'

import { useState, useEffect } from 'react'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Separator } from '@/components/ui/separator'

interface Service {
  id: string
  title: string
  description: string
  icon: string
  features: string[]
}

interface ServicesContent {
  title: string
  subtitle: string
  description: string
  services: Service[]
  benefits: {
    title: string
    description: string
  }[]
  cta: {
    title: string
    description: string
    buttonText: string
  }
}

const defaultContent: ServicesContent = {
  title: "Healthcare Services",
  subtitle: "Comprehensive Solutions for Medical Professionals",
  description: "At Amber, we go beyond just providing medical equipment. We offer a full range of healthcare services designed to support medical professionals in delivering exceptional patient care.",
  services: [
    {
      id: "1",
      title: "Equipment Procurement",
      description: "Access to premium medical equipment from leading manufacturers with competitive pricing and fast delivery.",
      icon: "üè•",
      features: [
        "Wide selection of medical devices",
        "Competitive pricing",
        "Fast delivery options",
        "Bulk order discounts"
      ]
    },
    {
      id: "2",
      title: "Technical Support",
      description: "Expert assistance with equipment setup, troubleshooting, and maintenance to keep your practice running smoothly.",
      icon: "üîß",
      features: [
        "24/7 technical assistance",
        "On-site support available",
        "Equipment calibration services",
        "Preventive maintenance"
      ]
    },
    {
      id: "3",
      title: "Training Programs",
      description: "Comprehensive training sessions to ensure your team can effectively use all medical equipment.",
      icon: "üéì",
      features: [
        "Hands-on training sessions",
        "Online tutorials",
        "Customized training programs",
        "Certification support"
      ]
    },
    {
      id: "4",
      title: "Consultation Services",
      description: "Expert advice on equipment selection, practice optimization, and healthcare technology trends.",
      icon: "üí¨",
      features: [
        "Equipment selection guidance",
        "Practice workflow optimization",
        "Technology trend analysis",
        "Regulatory compliance support"
      ]
    }
  ],
  benefits: [
    {
      title: "Enhanced Patient Care",
      description: "Our services help you deliver better patient outcomes through reliable equipment and expert support."
    },
    {
      title: "Time Savings",
      description: "Streamline your operations with our comprehensive service solutions that reduce administrative burden."
    },
    {
      title: "Cost Efficiency",
      description: "Maximize your investment with our competitive pricing and value-added services."
    },
    {
      title: "Expert Support",
      description: "Access to healthcare professionals with years of experience in medical equipment and practice management."
    }
  ],
  cta: {
    title: "Ready to Enhance Your Practice?",
    description: "Join thousands of healthcare professionals who trust Amber for their medical equipment and service needs.",
    buttonText: "Explore Our Services"
  }
}

export default function ServicesPage() {
  const [content, setContent] = useState<ServicesContent>(defaultContent)
  const [isEditing, setIsEditing] = useState(false)
  const [editedContent, setEditedContent] = useState<ServicesContent>(defaultContent)

  useEffect(() => {
    const savedContent = localStorage.getItem('amberServicesContent')
    if (savedContent) {
      const parsed = JSON.parse(savedContent)
      setContent(parsed)
      setEditedContent(parsed)
    }
  }, [])

  const handleSave = () => {
    setContent(editedContent)
    localStorage.setItem('amberServicesContent', JSON.stringify(editedContent))
    setIsEditing(false)
  }

  const handleCancel = () => {
    setEditedContent(content)
    setIsEditing(false)
  }

  const handleServiceChange = (index: number, field: keyof Service, value: string | string[]) => {
    const newServices = [...editedContent.services]
    newServices[index] = { ...newServices[index], [field]: value }
    setEditedContent({ ...editedContent, services: newServices })
  }

  const handleBenefitChange = (index: number, field: keyof typeof editedContent.benefits[0], value: string) => {
    const newBenefits = [...editedContent.benefits]
    newBenefits[index] = { ...newBenefits[index], [field]: value }
    setEditedContent({ ...editedContent, benefits: newBenefits })
  }

  const handleCtaChange = (field: keyof typeof editedContent.cta, value: string) => {
    setEditedContent({ ...editedContent, cta: { ...editedContent.cta, [field]: value } })
  }

  if (isEditing) {
    return (
      <div className="min-h-screen bg-gray-50">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-3xl font-bold text-gray-900">Edit Services Page</h1>
            <div className="space-x-2">
              <Button onClick={handleSave} className="bg-green-600 hover:bg-green-700">
                Save Changes
              </Button>
              <Button onClick={handleCancel} variant="outline">
                Cancel
              </Button>
            </div>
          </div>

          <div className="space-y-6">
            <Card>
              <CardContent className="space-y-4 p-6">
                <div>
                  <Label>Page Title</Label>
                  <Input
                    value={editedContent.title}
                    onChange={(e) => setEditedContent({ ...editedContent, title: e.target.value })}
                  />
                </div>
                <div>
                  <Label>Subtitle</Label>
                  <Input
                    value={editedContent.subtitle}
                    onChange={(e) => setEditedContent({ ...editedContent, subtitle: e.target.value })}
                  />
                </div>
                <div>
                  <Label>Description</Label>
                  <Textarea
                    value={editedContent.description}
                    onChange={(e) => setEditedContent({ ...editedContent, description: e.target.value })}
                    rows={3}
                  />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="space-y-4 p-6">
                <h3 className="text-lg font-semibold">Services</h3>
                {editedContent.services.map((service, index) => (
                  <div key={service.id} className="space-y-2 p-4 border rounded-md">
                    <h4 className="font-medium">Service {index + 1}</h4>
                    <Input
                      value={service.title}
                      onChange={(e) => handleServiceChange(index, 'title', e.target.value)}
                      placeholder="Service Title"
                    />
                    <Textarea
                      value={service.description}
                      onChange={(e) => handleServiceChange(index, 'description', e.target.value)}
                      placeholder="Service Description"
                      rows={2}
                    />
                    <Input
                      value={service.icon}
                      onChange={(e) => handleServiceChange(index, 'icon', e.target.value)}
                      placeholder="Icon (emoji or text)"
                    />
                    <div>
                      <Label>Features</Label>
                      {service.features.map((feature, featureIndex) => (
                        <Input
                          key={featureIndex}
                          value={feature}
                          onChange={(e) => {
                            const newFeatures = [...service.features]
                            newFeatures[featureIndex] = e.target.value
                            handleServiceChange(index, 'features', newFeatures)
                          }}
                          placeholder={`Feature ${featureIndex + 1}`}
                          className="mt-2"
                        />
                      ))}
                    </div>
                  </div>
                ))}
              </CardContent>
            </Card>

            <Card>
              <CardContent className="space-y-4 p-6">
                <h3 className="text-lg font-semibold">Benefits</h3>
                {editedContent.benefits.map((benefit, index) => (
                  <div key={index} className="space-y-2">
                    <Input
                      value={benefit.title}
                      onChange={(e) => handleBenefitChange(index, 'title', e.target.value)}
                      placeholder="Benefit Title"
                    />
                    <Textarea
                      value={benefit.description}
                      onChange={(e) => handleBenefitChange(index, 'description', e.target.value)}
                      placeholder="Benefit Description"
                      rows={2}
                    />
                  </div>
                ))}
              </CardContent>
            </Card>

            <Card>
              <CardContent className="space-y-4 p-6">
                <h3 className="text-lg font-semibold">Call to Action</h3>
                <div>
                  <Label>CTA Title</Label>
                  <Input
                    value={editedContent.cta.title}
                    onChange={(e) => handleCtaChange('title', e.target.value)}
                  />
                </div>
                <div>
                  <Label>CTA Description</Label>
                  <Textarea
                    value={editedContent.cta.description}
                    onChange={(e) => handleCtaChange('description', e.target.value)}
                    rows={2}
                  />
                </div>
                <div>
                  <Label>CTA Button Text</Label>
                  <Input
                    value={editedContent.cta.buttonText}
                    onChange={(e) => handleCtaChange('buttonText', e.target.value)}
                  />
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Admin Edit Button */}
        <div className="flex justify-end mb-4">
          <Button onClick={() => setIsEditing(true)} className="bg-green-600 hover:bg-green-700">
            Edit Page
          </Button>
        </div>

        {/* Hero Section */}
        <section className="bg-gradient-to-r from-green-600 to-green-800 text-white py-16 rounded-lg">
          <div className="max-w-4xl mx-auto text-center px-4">
            <h1 className="text-4xl md:text-5xl font-bold mb-4">{content.title}</h1>
            <p className="text-xl md:text-2xl text-green-100">{content.subtitle}</p>
          </div>
        </section>

        {/* Description */}
        <section className="py-16">
          <div className="max-w-4xl mx-auto text-center">
            <p className="text-gray-600 text-lg leading-relaxed">{content.description}</p>
          </div>
        </section>

        {/* Services */}
        <section className="py-16">
          <h2 className="text-3xl font-bold text-gray-900 mb-8 text-center">Our Healthcare Services</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {content.services.map((service) => (
              <Card key={service.id} className="hover:shadow-lg transition-shadow">
                <CardContent className="p-6">
                  <div className="flex items-start">
                    <div className="flex-shrink-0 w-16 h-16 bg-green-100 rounded-full flex items-center justify-center text-2xl mr-4">
                      {service.icon}
                    </div>
                    <div>
                      <h3 className="text-xl font-semibold text-gray-900 mb-2">{service.title}</h3>
                      <p className="text-gray-600 mb-4">{service.description}</p>
                      <ul className="space-y-2">
                        {service.features.map((feature, index) => (
                          <li key={index} className="flex items-start">
                            <span className="text-green-500 mr-2">‚úì</span>
                            <span className="text-gray-600">{feature}</span>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </section>

        {/* Benefits */}
        <section className="py-16 bg-gray-50 rounded-lg">
          <h2 className="text-3xl font-bold text-gray-900 mb-8 text-center">Why Choose Our Services?</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {content.benefits.map((benefit, index) => (
              <Card key={index}>
                <CardContent className="p-6 text-center">
                  <div className="w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                    <span className="text-green-600 font-bold">{index + 1}</span>
                  </div>
                  <h3 className="text-lg font-semibold text-gray-900 mb-2">{benefit.title}</h3>
                  <p className="text-gray-600">{benefit.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </section>

        {/* CTA Section */}
        <section className="py-16">
          <Card className="bg-gradient-to-r from-green-600 to-green-800 text-white">
            <CardContent className="p-8 text-center">
              <h2 className="text-3xl font-bold mb-4">{content.cta.title}</h2>
              <p className="text-xl text-green-100 mb-6">{content.cta.description}</p>
              <a href="/products" className="inline-block bg-white text-green-600 px-6 py-3 rounded-md font-semibold hover:bg-gray-100 transition-colors">
                {content.cta.buttonText}
              </a>
            </CardContent>
          </Card>
        </section>
      </div>
    </div>
  )
}
